:root {
	--main_color: #51bfad;
	--second_color: #303a6c;
	--bg-header-color: #5260ab;
}
.navbar-expand .navbar-nav {
	align-items: flex-end;
}
.nav-links {
	background: #ffffff;
	border-radius: 10px;
	line-height: 30px;
	width: 100%;
	padding: 0px 20px;
}
.nav-links a {
	font-size: 16px;
      font-weight: 500;
	letter-spacing: 0.5px;
      position: relative;
	display: block;
	transition: 0.5s;
	z-index: 1;
}
.nav-links a:hover {
	color: #fff !important;
}
.nav-links .active-link {
	color: #51bfad !important;
	color: var(--main_color) !important;
}
.nav-links a:before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border-top: 2px solid #51bfad;
	border-top: 2px solid var(--main_color);
	border-bottom: 2px solid #51bfad;
	border-bottom: 2px solid var(--main_color);
	-webkit-transform: scaleY(2);
	        transform: scaleY(2);
	opacity: 0;
	transition: 0.3s;
}
.nav-links a:after {
	content: "";
	position: absolute;
	top: 2px;
	left: 0;
	width: 100%;
	height: 95%;
	background-color: #51bfad;
	background-color: var(--main_color);
      color: #fff;
	-webkit-transform: scale(0);
	        transform: scale(0);
	opacity: 0;
	transition: 0.3s;
	z-index: -1;
}
.nav-links a:hover:before {
	-webkit-transform: scaleY(1);
	        transform: scaleY(1);
	opacity: 1;
}
.nav-links a:hover:after {
	-webkit-transform: scaleY(1);
	        transform: scaleY(1);
	opacity: 1;
}

.lang {
	color: #fff;
}
.navbar__search-bar {
	margin: 0px 10px;
}
.navbar__search-inp {
	position: relative;
	background-color: transparent;
	border: none;
	border-radius: 0px;
	border-bottom: 1px solid #ffffff;
	padding-right: 30px;
