}
.navbar__search-inp::-webkit-input-placeholder {
	color: #ffffff;
	padding-right: 0px;
}
.navbar__search-inp:-ms-input-placeholder {
	color: #ffffff;
	padding-right: 0px;
}
.navbar__search-inp::placeholder {
