/* Tablet screen */
@media screen and (max-width: 991px){
	.navbar__links{
		display: none;
	}
	.navbar__link-res{
		display: block;
		height: 60px;
	}
	.nav-links-res{
		position: relative;
		background-color: #fff;
		padding: 5px 20px;
		z-index: 999;
		border-radius: 4px;
	}
	.nav-links-res a{
		position: relative;
	}
	.nav-links-res a:hover::after{
		content: "";
		position: absolute;
		height: 45px;
		width: 8px;
		border-radius: 10px 0px 0px 10px;
		background-color: #51bfad;
		background-color: var(--main_color);
		right: -20px;
		top: -1px;
		/* border-right: 5px solid var(--main_color); */
	}
}
