	--bg-header-color-light: #525fab5b;
}
.ask__question{
      width: 100%;
      padding-top: 50px;
      overflow: hidden;
}
.ask__question-img-layout{
      position: relative;
      background-color: #5260ab;
      background-color: var(--bg-header-color);
      height: 500px;
      -webkit-clip-path: ellipse(100% 75% at 100% 50%);
              clip-path: ellipse(100% 75% at 100% 50%);
}
.ask__question-img-layout .student-img{
      position: absolute;
      width: 500px;
      height: 500px;
      object-fit: cover;
      z-index: 1;
      left: 0;
}
.ask__question-img-layout .student-pattern{
      position: absolute;
      width: 500px;
      height: 500px;
      top: 0;
      z-index: 0;
}

.ask__question-content{
      background-color: #fff;
      box-shadow: #e0e0e04d 0px 1px 2px 0px, #d3d3d326 0px 2px 4px 2px;
      padding: 20px;
      width: 100%;
      position: relative;
      right: -50px;
}
.ask__question-content h6{
      color: #F69C5D;
}
.ask__question-content-title{
      padding-right: 20px;
}
.ask__question-content-title > h3{
      font-size: 24px;
      color: #303a6c;
      color: var(--second_color);
}
.ask__question-content-title > span{
      font-size: 14px;
      color: #525fab71;
}

/* this properties to make float label */
.formfield {
	position: relative;
	box-sizing: border-box;
	margin: 10px auto;
	padding: 0px;
	border: 0px;
	font-size: 1em;
}
.formfield > input,
.formfield > label {
	padding: 1.2em;
	margin: 0.5em 0;
	width: 80%;
}
.formfield > input {
	border: 1px solid #ccc;
	border-radius: 4px;
	outline: none;
	position: relative;
}
.formfield > label {
	position: absolute;
	color: #51bfad;
	color: var(--main_color);
	padding: 0px 0.5em 0px 0.5em;
	margin: 0px;
	font-size: 0.8em;
	width: auto;
	height: 1.3em;
	background: white;
	transition: 0.2s ease-in-out;
	top: 0em;
	right: 2em;
}
/* end */

